import React from "react"
import {
  Container,
  Button,
  Col,
  Media,
  Image,
  Row,
  Form,
} from "react-bootstrap"

const ContactUs = () => (
  <Container fluid className="home-container p-0">
    <Container fluid className="py-xl-1">
      <Container fluid="xl" className="py-xl-1">
        <h1 className="pt-3 mt-4 h1-header">Contact Us</h1>
        <Row>
          {" "}
          <Col
            lg={{ order: "first" }}
            md={{ order: "first" }}
            className="pl-xl-4"
          >
            <p>
              Jump Loans is here for you whenever you need assistance. Whether
              you have questions about your current Jump Loans account, you’d
              like more info about your line of credit options before applying,
              or you’re just curious about what we offer, we’re happy to help!
            </p>
            <Form>
              <Form.Group controlId="Name">
                <Form.Label> Full Name </Form.Label>{" "}
                <Form.Control type="text" placeholder="Your Name" />
              </Form.Group>
              <Form.Group controlId="Email">
                <Form.Label> Email address </Form.Label>{" "}
                <Form.Control type="email" placeholder="Enter email" />
                <Form.Text className="text-muted">
                  We 'll never share your email with anyone else.{" "}
                </Form.Text>{" "}
              </Form.Group>
              <Form.Group controlId="Subject">
                <Form.Label> Subject </Form.Label>{" "}
                <Form.Control type="text" placeholder="Subject" />
              </Form.Group>
              <Form.Group controlId="Message">
                <Form.Label> Message </Form.Label>{" "}
                <Form.Control as="textarea" rows={3} />{" "}
              </Form.Group>
              <Button variant="primary" type="submit" className="w-100">
                Submit{" "}
              </Button>{" "}
            </Form>{" "}
          </Col>
          <Col lg={{ order: "last" }} md={{ order: "first" }}>
            <Container>
              <h3 className="h3-sub-header"> Let 's Chat</h3>{" "}
              <p>
                Speak with one of our knowledgable customer service
                representatives.{" "}
              </p>{" "}
              <Button variant="primary" type="submit" className="w-100">
                Let 's Chat{" "}
              </Button>{" "}
            </Container>{" "}
            <Container className="pt-5">
              <Row>
                <Col xs={12} className="">
                  <h3 className="m-0 h3-sub-header"> Support Email </h3>{" "}
                  <p className="m-0"> support @jumploans.com </p>{" "}
                </Col>{" "}
              </Row>{" "}
              <Row>
                <Col xs={12} className="mt-3 ">
                  <h3 className="m-0 h3-sub-header"> Phone </h3>{" "}
                  <p className="m-0"> (850 990 - JUMP) </p>{" "}
                </Col>{" "}
              </Row>{" "}
              <Row>
                <Col xs={12} className="mt-3">
                  <h3 className="m-0 h3-sub-header "> Operating Hours </h3>{" "}
                  <p className="m-0"> Monday - Saturday </p>{" "}
                  <p className="m-0"> 9 AM - 6 PM EST </p>{" "}
                </Col>{" "}
              </Row>{" "}
              <Row className="pb-5">
                <Col xs={12} className="mt-3">
                  <h3 className="m-0 h3-sub-header"> Go old - school mail </h3>{" "}
                  <p className="m-0"> Jump Loans Operations Dept. </p>{" "}
                  <p className="m-0"> PO Box 18613, Atlanta, GA 30326. </p>{" "}
                </Col>{" "}
              </Row>{" "}
            </Container>{" "}
          </Col>{" "}
        </Row>{" "}
      </Container>{" "}
    </Container>{" "}
  </Container>
)

export default ContactUs
